.content
    position: fixed
    top: 0
    right: 0
    bottom: 0
    left: 0
    background: url('../../public/img/1.jpg') center / cover
    display: grid
    justify-items: center
    align-items: start
    .title
        margin-top: 18vh
        font-style: 46px
        font-weight: 200
        padding: 4px 58px
        border-radius: 100vw
        background-color: rgba(255, 255, 255, 0.3)
        box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.20)
        
